@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);

html, body, #root{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: sans-serif;
  overflow: hidden;
}

body, html, .App, #root{
    width: 100%;
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

menu{
    width: 4em;
    height: 4em;
    border-radius: 10px;
    text-align: center;
    background-color: red;
    position: absolute;

}

.speechBouble {
    background: lightblue;
    padding: 1.5em;
    max-width: 20vw;
    min-width: 20vw;
    border-radius: 5px;
    align-items: flex-start;
    background-color: lightblue;
    color: #000;
    position: relative;
    font-size: 1.5vw;
    margin-top: -4em;
  }
  
  .speechBouble:before {
    content: "";
    width: 0px;
    height: 0px;
    position: absolute;
    border-left: 10px solid transparent;
    border-right: 10px solid lightblue;
    border-top: 10px solid lightblue;
    border-bottom: 10px solid transparent;
    right: 19px;
    bottom: -19px;
  }
